<template>
  <div class="card" v-if="task">
    <h2>{{ task.title }}</h2>
    <p><strong>Статус</strong>: <AppStatus :type="task.status"/></p>
    <p><strong>Дэдлайн</strong>: {{ new Date(task.date).toLocaleDateString() }}</p>
    <p><strong>Описание</strong>: {{ task.description }}</p>
    <div>
      <button class="btn" @click="changeTaskStatus('active')">Взять в работу</button>
      <button class="btn primary" @click="changeTaskStatus('done')">Завершить</button>
      <button class="btn danger" @click="changeTaskStatus('cancelled')">Отменить</button>
    </div>
  </div>
  <h3 class="text-white center" v-else>
    Задачи с id = <strong>Tут АЙДИ</strong> нет.
  </h3>
</template>

<script>
import AppStatus from '../components/AppStatus'
import { computed } from "vue";
import store from "@/store";
import { useRoute, useRouter } from 'vue-router'

export default {
  setup() {
    const route = useRoute()
    const router = useRouter()

    const task = computed(() => {
      return store.getters.taskById(+route.params.id)
    });

    const changeTaskStatus = (status) => {
      const updateStatus = {...task.value, status}
      store.dispatch('changeTask', updateStatus)

      router.push('/')
    }

    return{
     task,
      changeTaskStatus
    }
  },
  components: {AppStatus}
}
</script>

